@mixin title(){
  height: 54px;
  line-height: 54px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: 600;
}
.project_detail_wrapper{
  .project_detail_innner{
    background: #fff;
  }
  .detail_content{
    padding: 20px;
    box-sizing: border-box;
  }
 .record{
    border: 1px solid #F1F3F5;
    box-sizing: border-box;
    border-radius: 4px;
  }
  // 顶部
  .project_detail_header{
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid #F1F3F5;
    padding: 0 20px;
    font-size: 16px;
    // 审核
    .review_box{
      float: right;
    }
    .review{
      display: inline-block;
      padding: 5px 10px 0 10px;
      color: #333;
      margin-left: 20px;
    }
    .icon-shiliangzhinengduixiang9,
    .icon-shiliangzhinengduixiang10{
      color: #26a2ff;
      font-size: 18px;
      vertical-align: middle;
    }
    .icon-shiliangzhinengduixiang8,
    .icon-shiliangzhinengduixiang6,
    .icon-shiliangzhinengduixiang7{
      color: #26a2ff;
      font-size: 16px;
      margin: 0 4px 0 0;
      vertical-align: middle;
    }
  }
  // 右边
  .record{
    padding-bottom: 20px;
    .right_title{
      @include title();
    }
    // 记录列表
    .record_item{
      border-top: 1px solid #F1F3F5;
      padding: 10px;
    }
    .record_item_title{
      color: #333;
      line-height: 32px;
    }
    .record_item_person{
      color: #999;
    }
    .user_name{
      min-width: 100px;
      display: inline-block;
    }
    // 添加按钮
    .add_follow{
      float: right;
      line-height: 54px;
      margin: 4px 10px 0 0;
      cursor: pointer;
      i{
        font-size: 32px;
        font-weight: bold;
      }
      
    }
    .follow_date{
      display: inline-block;
      margin-left: 30px;
    }
    // 删除
    .del_follow{
      margin-left: 40px;
      cursor: pointer;
      color: #4D8EFF;
    }
    // 更多
    .record_loading_more{
      text-align: center;
    }
  }
  // 任务列表
  .detail_task_list{
    border: 1px solid #F1F3F5;
    margin-top: 20px;
    border-radius: 4px;
    .right_title{
      @include title();
      border-bottom:  1px solid #F1F3F5;
    }
    .detail_task_list_box{
      padding: 10px;
    }
    .task_right_btn{
      float: right;
      margin-right: 20px;
      color: #333;
      font-weight: 600;
      font-size: 14px;
      cursor: pointer;
    }
  }
}