.header_wrapper{
  width: 100%;
  min-width: 1200px;
  .header_menu{
    padding: 0 50px;
    box-sizing: border-box;
    .ant-menu-item-selected,.ant-menu-submenu-selected{
      background-color: rgba(44,112,231,1) !important;
    }
  }
  .floatRight {
    float: right;
  }
}

.header_menu .ant-menu-item:hover,
.header_menu .ant-menu-item-active,
.header_menu .ant-menu-submenu-active,
.header_menu .ant-menu-submenu-open,
.header_menu .ant-menu-submenu-selected,
.header_menu .ant-menu-submenu-title:hover{
  color: #fff !important;
}
