#uploadComponent-box {
  .uploadLabel {
    width: 100px;
    line-height: 1.499;
    font-weight: 400;
    text-align: center;
    touch-action: manipulation;
    cursor: pointer;
    border: 1px solid #d9d9d9;
    white-space: nowrap;
    padding: 0 15px;
    font-size: 14px;
    border-radius: 4px;
    height: 32px;
    position: relative;
    display: flex;
    align-items: center;
    box-shadow: 0 2px 0 rgba(0,0,0,.015);
  }
  .uploadLabel:hover {
    color: #40a9ff;
    border-color: #40a9ff;
  }
  .upload-area {
    width: 100%;
    padding-top: 10px;
    i {
      cursor: pointer;
    }
    p {
      height: 20px;
      margin-bottom: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .fileName {
        width: 350px;
        display: inline-block;
        overflow: hidden;
        text-overflow:ellipsis;
        whitewhite-space: nowrap;
      }
    }
  }
}
