#login-box {
  width: 100%;
  min-width: 1200px;
  height: 100%;
  overflow: hidden;
  background-image: url("../../assets/img/backgroundImg.png");
  background-size: 100% 100%;
  .card {
    position: absolute;
    right: 15%;
    top: 28%;
    .ant-btn {
      height: 45px;
      width: 280px;
      border-radius: 30px;
    }
    h3 {
      font-size: 36px;
      font-family: "PingFang SC";
      margin-bottom: 80px;
    }
    .ant-input {
      border-top: none;
      border-left: none;
      border-right: none;
      height: 45px;
      font-size: 16px;
      background: #f7f8fe;
    }
    .ant-input:focus {
      box-shadow: none;
    }
  }
}
