.report_list_wrapper{
  background: #fff;
  .header{
    height: 54px;
    line-height: 54px;
    border-bottom: 1px solid #ddd;
    padding: 0 20px;
    box-sizing: border-box;
    .right_btn{
      float: right;
      line-height: 54px;
      cursor: pointer;
      i{
        font-weight: bold;
        font-size: 24px;
        margin-right: 4px;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  .filter_search{
    padding: 20px;
  }
  .list_table{
    padding: 20px;
  }
}