.user_list_wrapper{
  background: #fff;
  .header{
    height: 54px;
    line-height: 54px;
    box-sizing: border-box;
    .title {
      width: 100%;
      height: 54px;
      line-height: 54px;
      padding: 0 20px;
      box-sizing: border-box;
      border-bottom: 1px solid #f0f2f5;
      // 添加任务
      .add_user{
        float: right;
        line-height: 54px;
        cursor: pointer;
        i{
          font-weight: bold;
          font-size: 24px;
          margin-right: 4px;
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }
  .filter_search{
    padding: 20px 20px 0 20px;
  }
  .list_table{
    padding: 20px;
  }
  .operate{
    padding: 2px 4px;
    margin-right: 10px;
    cursor: pointer;
    &:hover{
      color: rgba(116,124,251,1);
    }
  }
}
// 表单样式调整
.add_user_modal{
  width: 550px !important;
  .ant-advanced-search-form .ant-form-item{
    line-height: 32px;
    margin-bottom: 10px;
  }
  .ant-form-item-label{
    width: 86px;
    text-align: left;
  }
}

// 筛选
.search_btn{
  margin-top: 20px;
}