#delay-box {
  height: 100%;
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  .risk-userlist {
    width: 400px;
  }
  textarea {
    width: 400px;
    height: 100px;
  }
  h4 {
    font-size: 20px;
  }
  .ant-card-body {
    .p {
      font-size: 14px;
      color: #888888;
      display: flex;
      margin-bottom: 10px;
    }
    .content {
      color: #000000;
      display: inline-block;
      width: 260px;
    }
  }
}
