#program-box {
  p {
    margin: 0;
  }
  background-color: #fff;
  min-height: 100%;
  .title{
    height: 54px;
    border-bottom: 1px solid #f0f2f5;
    padding: 0 20px;
    line-height: 54px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    justify-items: center;
  }
  .list-content {
    padding: 20px;
    box-sizing: border-box;
  }
  .blue-color {
    color: rgb(76, 143, 255);
  }
}
