#taskList-box {
  width: 100%;
  box-sizing: border-box;
  background-color: #f7f8fe;
  overflow: hidden;
  .title {
    width: 100%;
    height: 55px;
    line-height: 55px;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f2f5;
  }
  .data-filter {
    width: 100%;
    min-height: 70px;
    display: flex;
    align-items: center;
  }
  .show-data-content {
    width: 100%;
    margin-top: 30px;
    padding: 0 20px 20px 20px;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: #fff;
  }
  .data-content {
    height: 100%;
    background-color: #fff;
  }
}
