#addTask-box {
  width: 100%;
  height: 100%;
  background-color: #f0f2f5;
  .content-box {
    width: 100%;
    height: 100%;
    background-color: #fff;
  }
  .ant-card-bordered {
    border: none;
  }
  .ant-input {
    width: 400px;
  }
  .ant-form-item {
    display: flex;
  }

  .dept-list-box {
    width: 700px;
    height: 300px;
    overflow: scroll;
    border: 2px solid #F2F9FF;
    padding: 20px;
    box-sizing: border-box;
    .ant-checkbox-wrapper {
      display: flex;
      align-items: center;
      width: 30%;
    }
  }
  .dept-list-box::-webkit-scrollbar {
    display: none;
  }
  .DeptName {
    font-weight: 600;
    width: 100%;
    color: #333333;
    text-align: center;
    line-height: 30px;
    background-color: #F2F9FF;
  }
  .Staff {
    display: flex;
    flex-wrap: wrap;
  }
  p {
    margin: 0;
  }
  .uploadLabel {
    line-height: 1.499;
    font-weight: 400;
    text-align: center;
    touch-action: manipulation;
    cursor: pointer;
    border: 1px solid #d9d9d9;
    white-space: nowrap;
    padding: 0 15px;
    font-size: 14px;
    border-radius: 4px;
    height: 32px;
    position: relative;
    display: flex;
    align-items: center;
    box-shadow: 0 2px 0 rgba(0,0,0,.015);
  }
  .uploadLabel:hover {
    color: #40a9ff;
    border-color: #40a9ff;
  }
  .upload-area {
    width: 100%;
    margin-top: -15px;
    margin-bottom: 20px;
    i {
      cursor: pointer;
    }
    p {
      width: 300px;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
