#phase-box {
  height: 100%;
  background-color: #fff;
  .title{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 54px;
    border-bottom: 1px solid #f0f2f5;
    padding: 0 20px;
    line-height: 54px;
    box-sizing: border-box;
  }
  .table-content {
    padding: 20px;
    box-sizing: border-box;
  }
  .ant-btn{
    margin-left: 10px;
  }
  .table-title {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }
}
