.projectStatusItem_wrapper{
  // 项目
  .project_list{
    border-radius: 4px;
    border: 1px solid rgb(221, 221, 221);
    border-bottom: none;
    background: #fff;
    font-size: 16px;
  }
  // 项目状态表格
  .status{
    height: 48px;
    background: #ddd;
    padding-left: 20px;
    .status_name{
      float: left;
      height: 48px;
      line-height: 48px;
      background: #ddd;
      font-size: 16px;
    }
    .more{
      line-height: 48px;
      float: right;
      margin-right: 20px;
      font-size: 16px;
    }
  }
}