 .project_wrapper{
   height: 100%;
  // 项目
  .project_list{
    border-radius: 4px;
    border: 1px solid rgb(221, 221, 221);
    background: #fff;
    font-size: 16px;
  }
  // 项目状态表格
  .status{
    height: 48px;
    line-height: 48px;
    background: #ddd;
    padding: 0 20px;
    .status_name{
      float: left;
      height: 48px;
      line-height: 48px;
      background: #ddd;
      font-size: 16px;
      box-sizing: border-box;
      margin: 0;
      padding: 0;
    }
    .more{
      line-height: 48px;
      float: right;
      margin-right: 20px;
      font-size: 16px;
      margin: 0;
      cursor: pointer;
    }
  }
   // 任务
   .task_box{
      margin-top: 30px;
    }
    .task_content{
      padding: 10px 20px;
      &:hover{
        transition: all .3s, height 0s;
        background: rgba(230, 247, 255, 1);
      }
    }
 }
