
#taskDetail-box {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  background-color: #f7f8fe;
  p {
    margin: 0;
  }
  .second-title {
    font-size: 16px;
    font-weight: 600;
  }
  .card-title {
    width: 100%;
    height: 40px;
    border-bottom: 1px solid #ddd
  }
  .content-box {
    width: 100%;
    min-height: 100%;
    margin: 30px auto;
    background: #fff;
  }
  .header-box {
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #ddd;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .ant-btn {
      margin-right: 10px;
    }
  }
  .showdata-box {
    width: 100%;
    min-height: calc(100% - 60px);
    display: flex;
    justify-content: space-around;
    li {
      width: 100%;
      min-height: 20px;
      display: flex;
      justify-content: flex-start;
      font-size: 16px;
      margin: 8px 0;
      .name {}
      .content { }
    }
  }
  .showdata-box-left {
    width: 700px;
    height: auto;
    .name {width: 120px}
    .content {width: 560px}
  }

  .attachments-item {
    //border-bottom: 1px solid #f0f2f5;
    padding-bottom: 10px;
    text-align: right;
  }


  .showdata-box-right {
    width: 450px;
    height: 100%;
  }
  .ant-card-bordered {
    margin: 30px 0;
  }


  .Recording-typeTwo {
    p{
      margin: 10px 0;
    }
  }
  .nameAndjob {
    margin: 10px 0;
  }
  .Recording-content {
    padding: 15px 10px;
    border-radius: 8px;
    box-sizing: border-box;
    background-color: #f1f3f5;
    margin: 10px 0;
  }
}
