#risk-box {
  height: 100%;
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  .ant-input {
    width: 400px;
    height: 150px;
  }
  h4 {
    margin: 15px 0;
    font-size: 20px;
  }
  .List-people {
    width: 400px;
    height: auto;
    .ant-row {
      margin: 10px 0;
    }
    .name {
      font-size: 16px;
      color: #000;
      margin-right: 10px;
    }
    .phone {
      color: #999;
      margin-right: 10px;
    }
    .dept {
      display: inline-block;
      font-size: 10px;
      background-color: #CFD0D1;
      padding: 0 4px;
      border-radius: 5px;
      color: #333;
      margin-right: 10px;
    }
  }
}
