.project_taskList_box {
  width: 100%;
  box-sizing: border-box;
  background: #fff;
  border-radius: 4px;
  .title {
    width: 100%;
    height: 54px;
    line-height: 54px;
    padding: 0 20px;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f2f5;
    // 添加任务
    .add_task{
      float: right;
      line-height: 54px;
      cursor: pointer;
      i{
        font-weight: bold;
        font-size: 24px;
        margin-right: 4px;
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  .top_base_info{
    padding: 20px 0;
    .col_item{
      height: 36px;
    }
    .search_row{
      margin-top: 20px;
    }
    .search{
      width: 218px;
      height: 32px;
      line-height: 32px;
      border-radius: 4px;
      background-color: rgba(50, 141, 237, 1);
      color: rgba(255, 255, 255, 1);
      font-size: 14px;
      text-align: center;
      cursor: pointer;
    }
  }
  .show-data-content {
    width: 100%;
    padding: 0 20px 20px 20px;
    box-sizing: border-box;
    border-radius: 5px;
    background-color: #fff;
  }
  .data-content {
    height: 100%;
    background-color: #fff;
  }
}
