.project_task_list_wrapper{
  .taskitem_wrapper{
    width: 100%;
    min-height: 80px;
    padding: 10px 12px 22px 0;
    box-sizing: border-box;
    background: #fff;
    border-radius:6px;
    margin-bottom: 10px;
    border: 1px solid #F1F3F5;
    border-radius: 4px;
  }
  .taskitem_box {
    padding-left: 12px;
    height: 100%;
    .title {
      font-size: 16px;
      line-height: 26px;
      color: #333;
      cursor: pointer;
    }
    // 任务状态
    .taskitem_status{
      margin-top: 16px;
      display: flex;
      justify-content: space-between;
      color: #333;
      font-size: 12px;
    }
    .status_name{
      color: #999;
    }
  }
  @mixin taskitem-label ($color, $icon) {
    width: 42px; /* no */
    height: 20px; /* no */
    border-top-right-radius: 10px; /* no */
    border-bottom-right-radius: 10px; /* no */
    color: #fff;
    font-size: 12px; /* no */
    line-height: 20px; /* no */
    text-align: center;
    background-color: $color;
    position: relative;
    left: 0; /* no */
    // .icon-icon{
    //   position: absolute;
    //   display: inline-block;
    //   color: $icon;
    //   left: -3px; /* no */
    //   top: 19px; /* no */
    //   padding: 0;
    //   margin: 0;
    //   // font-size: 4px;
    // }
  }
  // 任务标签
  .task_tag{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    width: 100%;
    .right{
      color: #4D8EFF;
      font-size:12px;
      font-weight:500;
    }
  }
  /*任务状态*/
  .status_COMPLETED {
    @include taskitem-label(#53E185, #06A53E);
  }
  .status_CREATED {
    @include taskitem-label(#4D8EFF, #105BDC);
  }
  .status_CLOSED {
    @include taskitem-label(#B8B8B8, #9C9C9C);
  }
}
