#message-box {
	width: 100%;
	min-height: 100%;
	background-color: #fff;
	padding: 20px;
	box-sizing: border-box;
	.h4 {
		font-size: 18px;
		text-align: center;
	}
	.blueSpan {
		color: rgb(76, 143, 255);
	}
	.notRead {
		font-size: 18px;
		color: #000
	}
	.read {
		color: #999;
		font-size: 20px
	}
}
