@mixin title(){
  height: 54px;
  line-height: 54px;
  padding-left: 10px;
  font-size: 14px;
  font-weight: 600;
}
.detail_content_box{
  border: 1px solid #F1F3F5;
  box-sizing: border-box;
  border-radius: 4px;
   // 内容区域
   .detail_content_title{
    border-bottom: 1px solid #F1F3F5;
    @include title();
  }
  .detail_content_item{
    padding: 10px;
    margin: 0;
    li{
      line-height: 32px;
    }
  }
  // 人脉资源
  .resource-category {
    padding-top: 10px;
    font-size: 0;
    .resource_item_box{
      height: 30px;
    }
    .resource_item{
      display: inline-block;
      line-height: 30px;
      margin-left: 8px;
      font-size: 14px;
    }
    /* 人脉资源选择icon */
    .selected {
      color: #3e9eff;
      width: 18px; 
      display: inline-block;
      vertical-align: baseline;
      text-align: center;
      position: relative;
      top: 3px;
    }
    .not-select {
      border-radius: 50%;
      border: 1px solid #5e86fa; 
      width: 16px;
      height: 16px;
      display: inline-block;
      vertical-align: baseline;
      margin: 0;
      padding: 0;
      position: relative;
      top: 3px;
    }
  }
}